import {Menu as AntdMenu} from "antd";
import routes from "../../app/router";
import { Link } from "react-router-dom";
import { clearQueryString } from "../../app/store";
import { useLocation } from 'react-router-dom'
import { useState } from "react";

export function Menu() {
  const location = useLocation()
  const [current, setCurrent] = useState(location.pathname)

  const handleClick = (e) => {
    clearQueryString()
    setCurrent(e.key)
  }

  return (
    <AntdMenu 
      style={{
        overflow: 'hidden',
        height: '100vh',
        position: 'fixed',
        width: '201px',
        left: 0,
      }}
      onClick={handleClick}
      defaultSelectedKeys={[current]}
      mode="inline">
      {routes.map(v => <AntdMenu.Item key={v.path}>
        <Link to={v.path} key={'link'+v.path}>
          {v.label}
        </Link>
      </AntdMenu.Item>)}
    </AntdMenu>
  )
}