import { configureStore } from '@reduxjs/toolkit';
import questReducer from '../features/quest/questSlice';
import itemReducer from '../features/item/itemSlice';
import armorReducer from '../features/armor/armorSlice';
import weaponReducer from '../features/weapon/weaponSlice';
import qs from 'query-string';

export const store = configureStore({
  reducer: {
    quest: questReducer,
    item: itemReducer,
    armor: armorReducer,
    weapon: weaponReducer
  },
});

export const setQueryStringWithoutPageReload = qsValue => { 
  const newurl = window.location.protocol + "//" +
                 window.location.host + 
                 window.location.pathname + 
                 qsValue +
                 window.location.hash;

  window.history.pushState({ path: newurl }, "", newurl);
};

export const setQueryStringValue = ( 
  key, 
  value, 
  queryString = window.location.search
) => { 
   const values = qs.parse(queryString); 
   const newQsValue = qs.stringify({ ...values, [key]: value }); 
   setQueryStringWithoutPageReload(`?${newQsValue}`);
};

export const getQueryStringValue = ( 
  key, 
  queryString = window.location.search
) => { 
  const values = qs.parse(queryString); 
  return values[key];
};

export const clearQueryString = () => {
  setQueryStringWithoutPageReload('');
}